import React, { ReactElement } from 'react';
import { Button, Icon } from 'antd-mobile';
import Layer from '@/components/Layer';
import history from '@/utils/history';
import {
  AnswerInner,
  AnswerLabel,
  AnswerList,
  AnswerListBox,
  AnswerListItem,
  AnswerModal,
  AnswerTitle,
  CloseBox,
} from './styles';
import { ButtonBox } from '../styles';

interface AnserProps {
  hide: () => void;
}
const Answer: React.FC<AnserProps> = (props): ReactElement => {
  const { hide } = props;
  return (
    <AnswerModal>
      <AnswerInner>
        <AnswerTitle>
          答题卡
          <CloseBox>
            <Icon type="cross" onClick={(): void => hide()} />
          </CloseBox>
        </AnswerTitle>
        <AnswerListBox>
          <AnswerLabel>1.1 微积分</AnswerLabel>
          <AnswerList>
            <AnswerListItem className="true">1</AnswerListItem>
            <AnswerListItem>2</AnswerListItem>
            <AnswerListItem className="true">3</AnswerListItem>
            <AnswerListItem>4</AnswerListItem>
            <AnswerListItem className="true">5</AnswerListItem>
          </AnswerList>
        </AnswerListBox>
        <ButtonBox>
          <Button
            type="primary"
            onClick={(): void => {
              hide();
              history.push('/quick-result');
            }}
          >
            确定提交
          </Button>
        </ButtonBox>
      </AnswerInner>
    </AnswerModal>
  );
};

const answer = (): {
  openModal: () => void;
  hideModal: () => void;
} => {
  const openModal = (): void => {
    Layer.open({
      effect: 'bottom',
      needCloseTag: true,
      content: <Answer hide={hideModal} />,
    });
  };
  const hideModal = (): void => Layer.hide();
  return {
    openModal,
    hideModal,
  };
};

export default answer;
